body {
    font-family: 'Poppins', sans-serif;
    background-color: #f4f4f9;
    color: #333;
    margin: 0;
    padding: 0;
}

h2 {
    text-align: center;
    font-size: 24px;
    color: #333;  /* Darker color for better contrast */
    margin-bottom: 20px;
}

p {
    text-align: center;
    font-size: 18px;
    color: #2b2d42;
}

button {
    display: block;
    width: 100%;
    max-width: 300px;
    margin: 10px auto;
    padding: 15px;
    font-size: 16px;
    color: white;
    background-color: #0072ff;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.2s ease;
}

button:hover {
    background-color: #00c6ff;
    transform: translateY(-2px);  /* Floating effect on hover */
}

button:active {
    transform: translateY(1px) scale(0.98);  /* Button press effect */
}

.quiz-container {
    max-width: 800px;
    margin: 50px auto;
    padding: 30px;
    background-color: #fff;
    border-radius: 12px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    background: linear-gradient(135deg, #f4f4f9 0%, #ffffff 100%);  /* Subtle gradient */
    animation: fadeIn 0.5s ease-in-out;  /* Fade-in effect */
}

.score {
    font-size: 20px;
    color: #333;
    text-align: center;
}

.quiz-end {
    text-align: center;
    margin-top: 30px;
    font-size: 24px;
    color: #4a4e69;
}

.timer {
    font-size: 18px;
    font-weight: bold;
    color: #ff6347;
    margin: 10px 0;
    padding: 10px 20px;
    background-color: #ffe5e0;
    border-radius: 50px;
    display: inline-block;  /* Pill-shaped design */
}

.progress-bar-container {
    width: 100%;
    height: 10px;
    background-color: #e0e0df;
    border-radius: 50px;
    overflow: hidden;
    margin-bottom: 20px;
}

.progress-bar {
    height: 100%;
    background: linear-gradient(90deg, #00c6ff, #0072ff);
    border-radius: 50px;
    transition: width 0.4s ease;
}

.result-container {
    margin-top: 20px;
    padding: 20px;
    text-align: center;
    border-radius: 10px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
    background-color: #f7f7f9;
    animation: fadeIn 0.5s ease-in-out;
}

.result-success, .result-fail {
    padding: 15px;
    margin: 10px 0;
    border-radius: 8px;
    font-size: 18px;
}

.result-success {
    color: #28a745;
    background-color: #d4edda;
    border: 1px solid #c3e6cb;
}

.result-fail {
    color: #c82333;
    background-color: #f8d7da;
    border: 1px solid #f5c6cb;
}

.result-success:hover, .result-fail:hover {
    transform: scale(1.02);
    box-shadow: 0 2px 12px rgba(0, 0, 0, 0.15);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.result-score {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 15px;
}

@keyframes fadeIn {
    from { opacity: 0; }
    to { opacity: 1; }
}

@media (max-width: 600px) {
    button {
        width: 90%;
    }

    .quiz-container {
        padding: 20px;
    }

    h2 {
        font-size: 20px;
    }

    p {
        font-size: 16px;
    }
}
.incorrect-answers {
    margin-top: 20px;
    padding: 15px;
    background-color: #f8f9fa;
    border-radius: 10px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
}

.incorrect-answers h3 {
    color: #c82333;
    font-size: 20px;
    margin-bottom: 10px;
}

.incorrect-answers ul {
    list-style-type: none;
    padding: 0;
}

.incorrect-answers li {
    margin-bottom: 15px;
    padding: 10px;
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
}

.incorrect-answers p {
    margin: 5px 0;
    font-size: 16px;
}

.incorrect-answers p strong {
    color: #c82333;
}