/* Reset */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

/* General Navbar Styling */
.navbar {
  height: 70px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.1rem;
  border-bottom: 2px solid var(--border-color);
  transition: background-color 0.3s ease;
  padding: 0;
  margin: 0;
}

/* Theme Colors */
.navbar.dark {
  --bg-color: #1f2937;
  --text-color: #ffffff;
  --hover-color: #4ade80;
  --border-color: #d1d5db;
}

.navbar.light {
  --bg-color: #ffffff;
  --text-color: #1f2937;
  --hover-color: #3b82f6;
  --border-color: #e5e7eb;
}

/* Navbar Container */
.navbar-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 1300px;
  padding: 0 20px;
}

/* Navbar Logo Styling */
.navbar-logo {
  color: var(--text-color);
  font-size: 1.8rem;
  font-weight: bold;
  text-decoration: none;
  font-family: 'Poppins', sans-serif;
}

/* Logo Image Styling */
.navbar-logo img {
  height: 95px; /* Adjust height as needed */
  width: auto; /* Maintains aspect ratio */
  cursor: pointer;
  transition: transform 0.3s ease, opacity 0.3s ease;
}

.navbar-logo img:hover {
  transform: scale(1.1);
  opacity: 0.9;
}

/* Menu Icon (for Mobile) */
.menu-icon {
  display: none;
  font-size: 2rem;
  color: var(--text-color);
  cursor: pointer;
}

/* Nav Menu & Links */
.nav-menu {
  display: flex;
  list-style: none;
  text-align: center;
}

.nav-item {
  height: 70px;
}

.nav-links {
  color: var(--text-color);
  text-decoration: none;
  padding: 0.5rem 1.5rem;
  height: 100%;
  display: flex;
  align-items: center;
  font-family: 'Poppins', sans-serif;
  transition: color 0.3s ease;
  position: relative;
}

/* Hover Effect on Nav Links */
.nav-links::after {
  content: '';
  position: absolute;
  width: 0;
  height: 2px;
  bottom: 0;
  left: 50%;
  background-color: var(--hover-color);
  transition: width 0.3s ease, transform 0.3s ease;
  transform: translateX(-50%);
}

.nav-links:hover {
  color: var(--hover-color);
}

.nav-links:hover::after {
  width: 70%;
}

/* Responsive Styling */
@media screen and (max-width: 960px) {
  /* Menu Icon Displayed on Mobile */
  .menu-icon {
    display: block;
  }

  /* Mobile Nav Menu */
  .nav-menu {
    display: none;
    flex-direction: column;
    align-items: center;
    width: 100%;
    position: absolute;
    top: 70px;
    left: 0;
    backdrop-filter: blur(5px);
  }

  .nav-menu.active {
    display: flex;
  }

  .nav-item {
    width: 100%;
  }

  .nav-links {
    text-align: center;
    padding: 1.5rem;
    width: 100%;
    display: block;
    color: var(--text-color);
    border-bottom: 1px solid var(--border-color);
  }

  /* Logo Image Adjustment for Mobile */
  .navbar-logo img {
    height: 95px;
  }
}
