/* Reset some basic elements for consistency across browsers */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  body {
    font-family: 'Roboto', sans-serif;
    background-color: #f7f8fa;
    color: #333;
    margin: 0;
    padding: 0;
  }
  
  h1, h2 {
    font-size: 1.5rem;
    font-weight: 600;
    color: #333;
  }
  
  h1 {
    margin-bottom: 20px;
  }
  
  /* Main container for course selection */
  .container {
    text-align: center;
    margin-top: 80px;
  }
  
  .cards-container {
    display: flex;
    justify-content: center;
    gap: 20px;
    flex-wrap: wrap;
    padding: 20px;
  }
  
  .card {
    background-color: #f9f9f9;
  border-radius: 10px;
  padding: 15px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  text-align: center;
  cursor: pointer;
  width: 180px;
  }
  
  .card:hover {
    transform: translateY(-5px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  }
  
  .card img {
    width: 100%;
    height: auto;
    border-radius: 10px;
  }
  
  .card h2 {
    font-size: 1.2rem;
    color: #4a90e2; /* Color matching border */
    margin-top: 10px;
  }
  