body {
  background-image: url('/public/QCM/qcm.png');
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  background-repeat: no-repeat;
}
*{
  user-select: none;
}
.app {
  display: flex;
  justify-content: space-around;
  padding: 20px;
}

.task-column {
  width: 30%;
  padding: 10px;
  background: #f0f0f0;
  border-radius: 8px;
}

.task {
  margin: 10px 0;
  padding: 10px;
  background: #ffffff;
  border: 1px solid #ccc;
  border-radius: 4px;
}

button {
  margin-top: 5px;
  cursor: pointer;
}
